<!--    <router-link to="/">首页</router-link>-->
<!--    <router-link to="/about">关于</router-link>-->
    <keep-alive>
      <!--    使用keep-alive嵌套router-view实现对路由页面的缓存-->
<!--    路由缓存就是为了解决以上问题提供的一个浏览器缓存方案，目的是让用户在使用Vue这种单页面应用时，可以被系统自动记录用户在每个页面的操作。
这个期望与用户使用浏览器的习惯相同，当用户打开浏览器访问百度搜索数据时，可能会通过新的标签页访问淘宝浏览商品，
这时在两个标签页切换的过程中，百度和淘宝会保留用户在各自网站的操作记录。
虽然两个网站都是通过一个浏览器窗口展示的，但是对用户而言，每个标签页就是相互独立地存在。
路由缓存功能可以通过简单的配置，让路由页面在切换时，其特性与切换浏览器标签页的效果趋于一致。-->
<!--      加入<keep-alive>组件后，会发现路由切换时，所有的操作记录都会完整地保存下来，这是因为<keep-alive>直接包裹的组件，并不会因为组件切换而触发组件销毁的钩子函数。
在反复切换页面时仔细观察控制台便可发现，组件被缓存后只有第一次加载组件时会触发从beforeCreate()到mounted()的四个钩子函数，之后便不会有任何输出，
所以之后的视图切换并没有触发组件的重新初始化，这样之前访问时的操作数据就不会因为页面切换而消失了-->
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

