import Vue from 'vue';
import VueRouter from 'vue-router'  //同步加载会按照正常ESModule的加载规则，将需要使用的视图组件导入并解析为JavaScript对象。这种方式导入的视图组件在项目初始化时便参与整个项目的运行，由于项目采用脚手架进行依赖解析，所以使用同步加载的视图组件代码，会在生产环境直接合并到项目的核心依赖包内部。这种方式构建的视图组件会随着项目一起运行，但视图组件过多时，由于所有代码都被注入一个JavaScript文件，会导致应用在第一次加载依赖时下载核心JavaScript依赖文件消耗时间过长。
import WebStruct from "@/WebStruct.vue";
Vue.use(VueRouter)
const routes=[
    {
        path:'/',
        name:'WebStruct',
        component:WebStruct, //指定加载的实际的vue组件
        children: [
            {
                path: '/note/NoteOne',
                name: 'NoteOne',
                component:() => import(/*webpackChunkName:"User"*/ "../note/NoteOne.vue")
            },
            {
                path: '/note/HelloMarkdown',
                name:'HelloMarkdown',
                component:() => import(/*webpackChunkName:"User"*/ "../note/HelloMarkdown.vue")
            }

        ]
    },
    ]
const router=new VueRouter({
    mode:'hash',   //定义页面切换使用hash模式，优先使用hash模式
    routes
})

export default router